import React, { useState, useEffect, useContext } from "react";
import { Card, Button, Col, Row } from "@themesberg/react-bootstrap";
import { securedAxiosInstance } from "../../../axiosConfig";
import HtmlText from "../../components/HtmlToText/HtmlText";
import {
  VerticalTimeline,
  VerticalTimelineElement,
} from "react-vertical-timeline-component";
import "react-vertical-timeline-component/style.min.css";
import { useHistory } from "react-router-dom";
import Loader from "../../../components/Loader";
import "./showNotifications.css";
import Icon from "./timelineIcon";
import { AuthContext } from "../../../context/AuthContext";

// Registering service worker for notification
// Public Vapid key for notification
const publicVapidKey =
  "BELqxt1OA-x3CSPf2TMWA5vdZAljkYU3oNeQfY5SmT5uZm0N1LxGv_s6SU_O26z4nDQAtbCq4N-TZgbXyaOqb2M";

// Encryption of public VAPID key
function urlB64ToUint8Array(base64String) {
  var padding = "=".repeat((4 - (base64String.length % 4)) % 4);
  var base64 = (base64String + padding).replace(/\-/g, "+").replace(/_/g, "/");

  var rawData = window.atob(base64);
  var outputArray = new Uint8Array(rawData.length);

  for (var i = 0; i < rawData.length; ++i) {
    outputArray[i] = rawData.charCodeAt(i);
  }
  return outputArray;
}

// Sending endpoints to store in database
async function saveSubscription(subscription, year, studentID, degree) {
  const response = await securedAxiosInstance.post(`/notifications`, {
    Batch: year,
    notify: subscription,
    student: studentID,
    degree: degree,
  });
  // console.log(response);
}
/*******************************************************************************/

const ShowNotifications = () => {
  const {
    state: { profileData },
  } = useContext(AuthContext);
  const degree = profileData?.degree;
  const campus = profileData?.campus;
  const batch = profileData?.batch;
  const studentID = profileData?.id;

  const [start, setstart] = useState(0); //index to fetch notifications from
  const limit = 12; //no of notifications to be fetched at a time

  useEffect(() => {
    const registerServiceWorker = async () => {
      if (!("serviceWorker" in navigator)) {
        // toast.error("Service Worker is not supported in your browser.");
        return;
      }

      if (!("PushManager" in window)) {
        // toast.error("Push notifications are not supported in your browser.");
        return;
      }

      try {
        // Register the service worker
        const swRegistration = await navigator.serviceWorker.register(
          "./service-worker.js",
          { scope: "/" }
        );
        console.log("Service Worker registered");

        // Check if the user is already subscribed
        const subscription = await swRegistration.pushManager.getSubscription();
        let isSubscribed = !(subscription === null);

        if (isSubscribed) {
          console.log("User is already subscribed.");
        } else {
          // Subscribe the user
          const newSubscription = await swRegistration.pushManager.subscribe({
            userVisibleOnly: true,
            applicationServerKey: urlB64ToUint8Array(publicVapidKey),
          });

          console.log("User is subscribed");
          saveSubscription(newSubscription, batch, studentID, degree);
          isSubscribed = true;
        }
      } catch (error) {
        console.error(
          "Failed to register Service Worker or subscribe user:",
          error
        );
        // toast.error("Failed to enable notifications. Please try again.");
      }
    };

    registerServiceWorker();
  }, [publicVapidKey, batch]);

  const [notifications, setNotifications] = useState({
    loading: false,
    data: [],
    error: null,
  });
  const [showMoreButton, setShowMoreButton] = useState(true);
  const [initialLoader, setInitialLoader] = useState(true);

  const bgColor = {
    Placement: "#5b7cf3",
    Internship: "#2cc972",
    Update: "#77d1e8",
    Webinar: "#ffc111",
    Hackathon: "#8e5bf3",
  };

  const history = useHistory();
  useEffect(() => {
    fetchNotifications();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const fetchNotifications = async () => {
    setNotifications({ ...notifications, loading: true });
    try {
      const response = await securedAxiosInstance.get(
        `/announcements?_sort=createdAt:desc&batch=${batch}&_where[0][degrees_allowed_contains]=${degree}&campus=${campus}&_start=${start}&_limit=${limit}`
      );
      const tempNotif = response.data;
      const newNotif = notifications.data;

      if (tempNotif.length < limit) {
        setShowMoreButton(false);
      }

      tempNotif.map((item) => newNotif.push(item));
      setNotifications({ loading: false, data: newNotif, error: null });
      setstart(start + limit);
      setInitialLoader(false);
    } catch (error) {
      setNotifications({ loading: false, data: [], error: error });
      setInitialLoader(false);
      console.log(error);
    }
  };

  function Show_button() {
    if (showMoreButton) {
      return (
        <div className="d-flex justify-content-center mt-4">
          <Button
            className="view_more"
            variant="dark"
            disabled={notifications.loading}
            onClick={fetchNotifications}
          >
            {notifications.loading ? <Loader /> : "View More"}
          </Button>
        </div>
      );
    }
  }

  function show_Notifications() {
    if (notifications.data) {
      return (
        <>
          {notifications.data.length !== 0 && (
            <VerticalTimeline animate={false} layout={"1-column-left"}>
              {notifications.data.map((item, key) => (
                <VerticalTimelineElement
                  contentArrowStyle={{ display: "none" }}
                  contentStyle={{
                    background: "transparent",
                    boxShadow:
                      "0px 4px 8px -2px rgba(16, 24, 40, 0.1), 0px 2px 4px -2px rgba(16, 24, 40, 0.06)",
                    transform: "translateY(-5px)",
                    padding: "15px", // Added padding to the content area
                    borderRadius: "8px", // Rounded corners for a softer look
                  }}
                  onTimelineElementClick={() =>
                    history.push(
                      item.redirect_url === "/"
                        ? `/companies/${item.event_id}`
                        : item.redirect_url
                    )
                  }
                  icon={<Icon type={item.event_type} />}
                  iconStyle={{
                    backgroundColor: bgColor[item.event_type || "Update"],
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    width: "35px", // Set a fixed size for the icon container
                    height: "35px",
                    borderRadius: "50%", // Make the icon container circular
                  }}
                  key={key}
                >
                  <Card
                    className="bg-transparent"
                    style={{
                      cursor: "pointer",
                      overflowX: "hidden",
                      // padding: "10px",
                      // marginBottom: "15px",
                      borderRadius: "8px",
                      background: "#fff",
                      // boxShadow:
                      //   "0px 4px 8px -2px rgba(16, 24, 40, 0.1), 0px 2px 4px -2px rgba(16, 24, 40, 0.06)",
                    }}
                  >
                    <Card.Body className="p-0">
                      <Row>
                        <Col sm={5} className="d-flex align-items-center">
                          <div
                            className="image bg-black rounded-circle d-flex justify-content-center align-items-center"
                            style={{
                              height: "50px",
                              width: "50px",
                              marginRight: "10px",
                              overflow: "hidden",
                            }}
                          >
                            <div
                              className="text-logo"
                              style={{
                                color: "white",
                                fontSize: "1.8rem",
                                fontWeight: "bolder",
                              }}
                            >
                              {item.for.split(" ").length > 1
                                ? item.for.split(" ")[0].substring(0, 1) +
                                  item.for.split(" ")[1].substring(0, 1)
                                : item.for.split(" ")[0].substring(0, 1)}
                            </div>
                          </div>
                          <div className="d-flex flex-column">
                            <h5 style={{ margin: "0", fontSize: "1.1rem" }}>
                              {item.title}
                            </h5>
                            <h6 style={{ color: "#6f7274", marginTop: "4px" }}>
                              {item.for}
                            </h6>
                          </div>
                        </Col>
                        <Col
                          sm={7}
                          className="d-flex flex-column align-items-start"
                        >
                          <HtmlText htmlText={item.content} />
                          <div className="d-flex w-100 justify-content-between mt-2">
                            <p
                              style={{
                                margin: "0px",
                                fontWeight: "700",
                                fontSize: "0.9rem",
                              }}
                            >
                              {new Date(item.createdAt).toString().slice(0, 15)}
                              ,{" "}
                              {new Date(item.createdAt)
                                .toString()
                                .slice(16, 21)}
                            </p>
                            <div
                              className={`tag border rounded px-2 py-1 ${
                                item.event_type || "Update"
                              }`}
                              style={{
                                backgroundColor: "#f0f0f0", // Light background color for tags
                                fontWeight: "bold",
                                fontSize: "0.8rem",
                                width: "6rem",
                                textAlign: "center",
                              }}
                            >
                              {item.event_type || "Update"}
                            </div>
                          </div>
                        </Col>
                      </Row>
                    </Card.Body>
                  </Card>
                </VerticalTimelineElement>
              ))}
              {showMoreButton && (
                <VerticalTimelineElement
                  icon={
                    <img
                      src={require("../../../assets/img/icons/plus-solid.svg")}
                      alt="load_more"
                      style={{ width: "100%", height: "100%" }}
                    />
                  }
                  iconOnClick={fetchNotifications}
                  iconStyle={{
                    cursor: "pointer",
                    backgroundColor: "#e44e72",
                    width: "50px",
                    height: "50px",
                    borderRadius: "50%",
                  }}
                />
              )}
            </VerticalTimeline>
          )}
        </>
      );
    }
  }

  function show_loader() {
    if (notifications.loading) {
      return <Loader />;
    }
  }
  if (notifications.error) {
    return (
      <div>
        <h1>Error in fetching notifications.</h1>
      </div>
    );
  }
  if (initialLoader) {
    return <Loader />;
  }

  return (
    <div>
      {show_Notifications()}
      {show_loader()}
      {Show_button()}
    </div>
  );
};

export default ShowNotifications;
